%hover-effects {
    transform: scale(1.1);
    transition-duration: $time;
    transition-timing-function: $cubic;
}


img.hoover:hover {
    @extend %hover-effects
}



.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}


.hoover:hover {
    @extend %hover-effects;
}

        